import { Container } from 'react-bootstrap';
import { useEffect,useRef } from 'react';
import { Link } from 'react-router-dom';


function Body(){
    const ref = useRef();
    const ref2 = useRef();
    const ref3 = useRef();
    const ref4 = useRef();
    const ref5 = useRef();
    const Contstyle = {
        height: '90vh', width: '100vw',justifyContent: "center",display:"flex",alignItems: 'center',opacity:0, transition: 'all 0.8s',color:'white'
    }
    
    useEffect(() => {
              const observer = new IntersectionObserver(
                entries => {
                  if (entries[0].isIntersecting) {
                    console.log(entries)
                    entries[0].target.style.opacity=1;

                  }
                },
                { threshold: 0.9 }
              );
              //  
              observer.observe(ref.current); 
              observer.observe(ref2.current); 
              observer.observe(ref3.current); 
              observer.observe(ref4.current); 
              observer.observe(ref5.current); 
          }, []);
          

    return(
    <Container fluid style={{backgroundColor:"#44194C", height:'auto', marginBottom: '3rem'}}>
        <Container ref={ref} style={Contstyle}>
            <h1></h1>
        </Container>
        <Container ref={ref2}  style={Contstyle}>
            <h1>  </h1>
        </Container>
        <Container className='d-flex flex-row' ref={ref3} style={Contstyle}>
            <img src='asset/image2.jpeg' alt="2 " style={{width: '40vw', height: '45vh', marginRight: '1.5em'}}/>
            <h1>  ,</h1>
        </Container>
        <Container className='d-flex flex-row' ref={ref4} style={Contstyle}>
            <h1>  <br/> <br />  </h1>
            <img src='asset/image1.png' alt="1 " style={{width: '40vw', height: '45vh', marginLeft: '1.5em'}}/>
        </Container>
        <Container ref={ref5} style={Contstyle}>
            <h1> </h1>
        </Container>
        <Container style={{textAlign: 'center', height: '20vh'}}>
            <Link to='/emotion' style={{ textDecoration: 'none', color: 'white'}}><h3>   </h3></Link>
        </Container>
    </Container>
    );
}

export default Body